// const defaultConfig=parseModelJS(mockcfgstring);

const W = 800;
const H = 600;



function App() {
    const stageRef = useRef();
    const latestMousePos = useRef([400, 300]);
    const [timestamp, setTimestamp] = useState(performance.now());


    const [control, setControl] = useState(defaultConfig.getControl());
    useEffect(() => {
        let lastTime = performance.now();
        let canceled = false;
        const updateControl = (timestamp) => {
            if (canceled) return;
            const dt = timestamp - lastTime;
            lastTime = timestamp;
            setControl(control => {
                const x = latestMousePos.current[0];
                const y = latestMousePos.current[1];
                const ratio = Math.min(0.02 * dt, 1);
                const easeX = control.mouseX * (1 - ratio) + x * ratio;
                const easeY = control.mouseY * (1 - ratio) + y * ratio;
                const distance = Math.sqrt((easeX - control.mouseX) * (easeX - control.mouseX) + (easeY - control.mouseY) * (easeY - control.mouseY))
                if (distance < 1) return defaultConfig.getControl({mouseX:x, mouseY:y});
                return defaultConfig.getControl({mouseX:easeX, mouseY:easeY});
            })
            setTimestamp(timestamp);
            requestAnimationFrame(updateControl);
        }
        requestAnimationFrame(updateControl);
        return () => {
            canceled = true;
        }
    }, []);
    const handleMouseMove = (e) => {
        const stageRect = stageRef.current?.getBoundingClientRect();
        if (!stageRect) return;
        const mouseX = e.clientX - Math.floor(stageRect.x);
        const mouseY = e.clientY - Math.floor(stageRect.y);
        //setControl(getControl(mouseX,mouseY));
        latestMousePos.current = [mouseX, mouseY];
    }
    return (
        <div className="App">
            <div
                className="stage"
                style={{width: W + 'px', height: H + 'px'}}
                ref={stageRef}
                onMouseMove={handleMouseMove}
                onTouchMove={(e) => {
                    handleMouseMove(e.touches?.[0]);
                }}
            >
                <Miku control={control} timestamp={timestamp} model={defaultConfig.model}></Miku>
            </div>
        </div>
    );
}

export default App;
